import http from 'axios';
import Conf from './conf';

export default class Http extends Conf {
    GET = 'get';
    POST = 'post';
    PUT = 'put';
    //..

    GETURL = { protocol: '', request: '' };
    NODE;

    HTTP = http.create({
      timeout: 600000,
      withCredentials: true,
      headers: {
          // 'Content-type': 'application/json',
      },
    })

    constructor() {
        super();
        super.initConf();

        this.NODE = process.env.NODE_ENV || 'development';
        this.HTTP.interceptors.request.use(config => {
            return config
        }, error=> {
            // 
            return Promise.reject(error)
        })
    }

    // 
    getGlobalUrl() {
        switch (this.NODE) {
            case 'production': // 
                super.GETURL = this.CONF.child[this.CONF.requestProductionName];
                break;
            case 'development': // 
            default:
                super.GETURL = this.CONF.child[this.CONF.requestDevelopmentName];
                break;
        }
        return `${this.GETURL.protocol || 'http'}://${this.GETURL.request}`;
    }

    // 
    setGlobalHeader (headers) {
        if (!headers) return;
        this.HTTP.headers = {...this.HTTP.headers, ...headers};
    }

    /**
     * 
     * @param url
     * @param requestData
     * @returns {Promise<AxiosResponse<any>>}
     */
    async request(url = '', requestData = {method: this.POST, data: {}, params: {}}) {
        let result = await this.HTTP({
            url: url,
            Origin: "",
            headers: Object.assign({}, requestData.headers),
            method: requestData.method,
            data: requestData.data,
            params: requestData.params,
            body: requestData.body
        });

        return result;
    }

    /**
     * post 
     * @param url
     * @param data
     * @returns {Promise<AxiosResponse<any>>}
     */
    async post(url, data = {data: {}, params: {}}) {
        const _url = this.getGlobalUrl() + url;

        let result = await this.request(_url, {
            method: this.POST,
            headers: data.headers,
            params: data.params,
            data: data.data,
        });

        return result;
    }

    /**
     * get 
     * @returns {Promise<AxiosResponse<any>>}
     */
    async get(url = '', data = {data: {}, params: {}}) {
        const _url = this.getGlobalUrl() + url;

        let result = await this.request(_url, {
            method: this.GET,
            headers: data.headers,
            params: data.params,
            data: data.data,
        });

        return result;
    }

    /**
     * put 
     * @returns {Promise<AxiosResponse<any>>}
     */
    async put(url = '', data = {data: {}, params: {}}) {
        const _url = this.getGlobalUrl() + url;

        this.HTTP.headers = {...this.HTTP.headers, ...data.headers};

        let result = await this.HTTP({
            url:_url,
            method: this.PUT,
            headers: data.headers,
            params: data.params,
            data: data.data,
        })

        return result;
    }
}

